.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.imghome{
    width: 150px;
    height: 150px;
    margin-bottom: 30px;
    object-fit: cover;
    border-radius: 50%;
    background-position: center center;
    cursor: pointer;
    transform: scale(1);
    transition: 0.3s linear;
}
.imghome:hover{
    transform: scale(1.3);
}
.intro{
    padding: 20px;
    border-radius: 10px;
    max-width: 540px;
    background-color: antiquewhite;
    text-align: center;
    z-index: 10;
}
.home__img{
    margin-bottom: 1.5rem;
}
.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}
.home__social{
 display: flex;
 justify-content: space-between;
 align-items: center;
 column-gap: 1.75rem;
 margin: 1.5rem 0;
}
.home__social-link{
    width: 30px;
    text-align: center;
    color: var(--title-color);
    font-size: 1.3rem;
    transition: 0.3s;
}
.home__social-link:hover{
    background-color: hsl(43, 100%, 68%);
   border-radius: 50%;
   padding: 10px;
}
.scroll__down{
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}
.home__scroll-name{
    font-size: var(--small-font-size);
}
.mouse{
    border: 2px solid #454360;
    display: block;
    width: 1.25rem;
    height: 1.6rem;
    margin: auto;
    border-radius: 1rem;
    position: relative;
    margin-top: 0.75rem;
}
@keyframes ani-mouse{
    0%{
        top: 29%;
    }
    15%{
        top: 50%;
    }
    50%{
        top: 70%;
    }
    100%{
        top: 29%;
    }
}
.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: 0.5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}
.shapes{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.shape{
    position: absolute;
}
.s1{
    left: 2%;
    top: 10%;
}
.s2{
    left: 18%;
    top: 30%;
}
.s3{
    left: 5%;
    bottom: 30%;
}
.s4{
    left: 2%;
    bottom: 10%;
}
.s5{
    left: 44%;
    top: 10%;
}
.s6{
    left: 36%;
    bottom: 10%;
}
.s7{
    top: 20%;
    right: 25%;
}
.s8{
    right: 24%;
    bottom: 20%;
}
.s9{
    right: 2%;
    top: 10%;
}
.s10{
    top: 45%;
    right: 11%;
}
.s11{
    bottom: 10%;
    right: 2%;
}